                    </div>
                </li>
                <li :class="[curIndex==4?'active':'',showStatus[3]?'showactive':'']" @click="curtips('此功能暂未开发！')">
                    <div class="fmnav1">
                        <router-link :to="{path:'/others'}" replace>其他</router-link>
                    </div>
