    .fmainbtn button{
        background-color: transparent;
        border: none;
    }
    .fmainbtn button img{
        width: 40px;
        height: 40px;
    }
    .fmnavs{
        position: fixed;
        top: 0;
        left: 0;
        width: 160px;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);
        z-index: 101;
        transition: all 0.3s;
        transform: translateX(-170px);
    }
    .fmnavs.active{
        transform: translateX(0);
    }
    .fmnavs a{
        color: white;
        padding: 10px 16px;
        border-left: 4px solid transparent;
    }
    .fmnavs li{
        background-color: rgba(0, 0, 0, 0.7);
        border-bottom: 1px solid rgb(53, 53, 53);
        transform: translateX(-170px);
        transition: all 0.3s;
    }
    .fmnavs li.showactive{
        transform: translateX(0px);
    }
    .fmnavs li.active a{
       border-color: rgb(0, 160, 233);
    }
